.page {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px 0 0;
}

.card__title {
  color: #060b35;
  width: 100%;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  width: 100%;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 16px 0 0;
  display: flex;
}

.card__revievs {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin: 24px 0 0;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin: 16px 0 0;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

